<template>
  <div id="login">
    <header>验证手机号</header>
    <van-form class="formBox" ref="formRef" @submit="login">
      <!-- 手机号 -->
      <van-cell-group class="formCell telBox">
        <van-field
          placeholder="请输入手机号"
          v-model="formData.tel"
          :rules="formRule.tel"
          :error="false"
          name="tel"
          type="number"
        ></van-field>
      </van-cell-group>
      <!-- 验证码 -->
      <van-cell-group class="formCell codeBox">
        <van-field
          placeholder="请输入验证码"
          v-model="formData.code"
          :rules="formRule.code"
          :error="false"
          name="code"
          type="number"
          :maxlength="4"
        >
          <template #button>
            <van-button
              round
              block
              type="info"
              native-type="button"
              class="getCodeBtn"
              :class="{notClick:!getCodeFlag}"
              @click="getCode"
              :disabled="!getCodeFlag"
            >
              <span v-if="intervalFlag">({{times}})</span>
              获取验证码
            </van-button>
          </template>
        </van-field>
      </van-cell-group>
      <!-- 确认按钮 -->
      <van-cell-group class="formCell submitBox">
        <van-button round block type="info" native-type="submit" class="submitBtn">确定</van-button>
      </van-cell-group>
      <!--  -->
    </van-form>
  </div>
</template>

<script>
import { getPhoneCode, codeLogin } from "../assets/javascript/apis.js";

export default {
  data() {
    var checkCode = (value, rule) => {
      var regCode = /^\d+$|^\d+[.]?\d+$/;
      return regCode.test(value) && value.length == 4;
    };
    return {
      getCodeFlag: true,
      loginFlag: true,
      intervalFlag: false,
      times: 60,
      formData: {
        tel: "",
        code: "",
        codeKey: "",
      },
      formRule: {
        tel: [
          {
            required: true,
            message: "手机号不可为空",
            trigger: "onChange",
          },
          {
            pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/,
            message: "请输入正确格式的手机号",
            trigger: "onChange",
          },
        ],
        code: [
          {
            required: true,
            message: "验证码不可为空",
            trigger: "onChange",
          },
          {
            validator: checkCode,
            message: "验证码为长度为4的数字",
            trigger: "onChange",
          },
        ],
      },
    };
  },
  created() {},
  methods: {
    //获取验证码
    getCode() {
      this.$refs.formRef
        .validate("tel")
        .then((res) => {
          if (!this.getCodeFlag) return;
          this.getCodeFlag = false;
          const data = {
            mobile: this.formData.tel,
          };
          getPhoneCode(data).then((res) => {
            this.$toast({ position: "top", message: "发送验证码成功!" });
            if (res.code == 1000) {
              this.times = 60;
              this.intervalFlag = true;
              let _this = this,
                time = setInterval(function () {
                  if (_this.times <= 0) {
                    _this.getCodeFlag = true;
                    _this.intervalFlag = false;
                    clearInterval(time);
                  }
                  _this.times--;
                }, 1000);
              this.formData.codeKey = res.key;
            } else {
              this.$toast({ position: "top", message: res.message });
            }
          });
        })
        .catch((res) => {
          console.log(res);
        });
    },
    //登录提交
    login(val) {
      if (this.formData.codeKey) {
        if (!this.loginFlag) return;
        this.loginFlag = false;

        const data = {
          mobile: this.formData.tel,
          user_id: this.$store.state.WXUserInfo.id,
          verification_key: this.formData.codeKey,
          verification_code: this.formData.code,
        };

        codeLogin(data).then((res) => {
          // console.log(res);
          this.loginFlag = true;
          if (res.status == "success") {
            this.$toast({ position: "top", message: "登录成功" });

            this.$store.commit("setAppUserInfo", {
              token: res.token,
              expired_at: res.expired_at,
            });

            this.$router.push("/detail");
          } else {
            this.$toast({ position: "top", message: res.message });
          }
        });
      } else {
        this.$toast({ position: "top", message: "请先获取验证码" });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#login {
  padding: 0 40px;
  header {
    padding: 30px 0 60px;
    font-size: 24px;
    font-weight: 500;
    color: rgba(7, 7, 7, 1);
  }
  /deep/.formBox {
    .formCell {
      margin-bottom: 40px;
      &:after {
        border: none;
      }
      .van-field {
        width: 100%;
        height: 40px;
        padding: 0;
        overflow: inherit;
        .van-field__value {
          width: 100%;
          height: 40px;
          position: relative;
          .van-field__body {
            width: 100%;
            height: 40px;
            input {
              width: 100%;
              height: 40px;
              padding: 0 30px;
              font-size: 16px;
              background: rgba(242, 244, 246, 1);
              border-radius: 40px;
            }
          }
          .van-field__error-message {
            position: absolute;
            top: 100%;
            left: 30px;
            z-index: 10000;
          }
        }
      }
      &.codeBox {
        .getCodeBtn {
          width: 130px;
          height: 40px;
          background: linear-gradient(
            -10deg,
            rgba(76, 170, 241, 1),
            rgba(155, 120, 240, 1)
          );
          border-radius: 40px;
          font-size: 14px;
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
          border: none;
        }
        .notClick {
          background: rgba(241, 243, 245, 1);
          color: rgba(7, 7, 7, 1);
        }
      }
      &.submitBox {
        .submitBtn {
          background: linear-gradient(
            -10deg,
            rgba(76, 170, 241, 1),
            rgba(155, 120, 240, 1)
          );
          border-radius: 40px;
          font-size: 15px;
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
        }
      }
    }
  }
}
</style>